import Vue from 'vue';
import axios from 'axios';
import App from './App.vue';
import './registerServiceWorker';
import router from './router';
import store from './store';

Vue.config.productionTip = false;

// axios.defaults.baseURL = `${process.env.NODE_ENV === 'production'
//   ? '' : 'https://cors-anywhere.herokuapp.com/'}https://member.public.com.tw/raspapp/`;
axios.defaults.baseURL = 'https://cors-anywhere.herokuapp.com/https://member.public.com.tw/raspapp/';

axios.interceptors.request.use(
  (config) => {
    // Get token and add it to header 'Authorization'
    const { token } = store.state;
    if (token) {
      config.headers.Authorization = `Bearer ${token}`; // eslint-disable-line no-param-reassign
    }
    return config;
  },
  (error) => Promise.reject(error),
);

let isRefreshing = false;
let subscribers: any[] = [];

function subscribeTokenRefresh(cb: any) {
  subscribers.push(cb);
}

function onRefreshed() {
  subscribers.map((cb) => cb());
}

axios.interceptors.response.use(
  (response) => response,
  (err) => {
    const {
      config,
      response: { status, data },
    } = err;

    const originalRequest = config;

    if (data.message === 'Missing token') {
      router.push({ name: 'Login' });
      return Promise.reject();
    }

    if (originalRequest.url.includes('LoginMember')) {
      return Promise.reject(err);
    }

    if (status === 401) {
      if (!isRefreshing) {
        isRefreshing = true;
        store
          .dispatch('refreshToken')
          .then((tokenStatus) => {
            if (tokenStatus === 200 || tokenStatus === 204) {
              isRefreshing = false;
              onRefreshed();
              subscribers = [];
            }
          })
          .catch((error) => {
            console.error(error);
          });
      }

      return new Promise((resolve) => {
        subscribeTokenRefresh(() => {
          originalRequest.headers.Authorization = `Bearer ${store.state.token}`;
          resolve(axios(originalRequest));
        });
      });
    }

    return Promise.reject();
  },
);

subscribers = [];

new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount('#app');
